





























































/**
 * @Describe: 设备报警记录
 * @Author: liuyang
 * @Date: 2019-11-21 14:09
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import genCascader from "@/utils/genCascader";

@Component({})
export default class DeviceAlarmRecord extends Vue {
  typeOptions: any[] = [];
  deviceTypeId: string = "";
  deviceName: string = "";
  time: string = "";
  readonly timeOptions: ReadonlyArray<any> = Object.freeze([
    { label: "最近5分钟", value: "five" },
    { label: "最近10分钟", value: "ten" },
    { label: "最近15分钟", value: "quarters" },
    { label: "最近30分钟", value: "twoQuarters" },
    { label: "最近45分钟", value: "threeQuarters" },
    { label: "最近60分钟", value: "hour" }
  ]);
  tableData: any[] = [];
  pageSize: number = 10;
  pageNum: number = 1;
  total: number = 0;
  loading: boolean = false;

  dialog: boolean = false;
  detailData: any[] = [];

  created(): void {
    this.getTypeOptions();
    this.getTableData();
  }

  // 获取设备类型树
  async getTypeOptions() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getDeviceTypeTree);
    if (status) {
      this.typeOptions = data;
      genCascader(this.typeOptions);
    } else {
      this.$message.error(message);
    }
  }

  // 报警记录
  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.getDeviceAlarmRecord, {
        current: this.pageNum,
        size: this.pageSize,
        query: {
          deviceName: this.deviceName,
          deviceTypeId: this.deviceTypeId ? this.deviceTypeId[this.deviceTypeId.length - 1] : "",
          time: this.time
        }
      });
      if (status) {
        if (data) {
          this.tableData = data.list;
          this.total = data.totalElements;
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }

  // 查看详细数据
  handleViewDetail(row: any) {
    this.dialog = true;
    this.detailData = row.warningValues.map((item: any) => item.attributeInfos).reduce((a: any[], b: any[]) => a.concat(b));
  }
}
